@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");

.project-site-wrapper {
  max-width: 1100px;
  margin: 18vh auto 0;

  @include media("<=1100px") {
    padding: 0 var(--padding-border) 35vh;
    margin: 0 auto -35vh;
  }
}

.project-site {
  display: flex;
  padding: 5vh 0 10vh;
  align-items: stretch;
  @include media("<=tablet") {
    flex-wrap: wrap;
    padding: 3vh 0;
  }
  &__name {
    margin: 10vh 0 2vh;
    position: relative;
    z-index: -1;
    width: 100%;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: -40px;
      z-index: 1;
      width: 6px;
      height: 100%;
      background-color: rgb(94, 94, 94);

      @include media("<=1100px") {
        left: -25px;
        width: 8;
      }
    }
  }
  &__title {
    position: relative;
    z-index: 9919;
    font-size: 72px;
    font-weight: 300;
    line-height: 2;
    font-family: raleway;
    margin: 13vh 0 0;
    line-height: 1.3;
    display: flex;
    flex-wrap: wrap;
    span {
      font-weight: 100;
    }
    @include media("<=tablet") {
      margin: 15vh 0 2vh;
      font-size: 52px;
    }
    @include media("<=phone") {
      margin: 15vh 0 2vh;
      font-size: 36px;
    }
  }
  &__date {
    font-size: 12px;
    color: var(--gray);
    opacity: 0.6;
    color: white;
    font-weight: 400;
    font-family: montserrat;
    position: relative;
    z-index: 999;
  }

  &__info {
    width: 50%;
    display: flex;
    flex-direction: column;
    position: relative;
    padding-right: 3vw;

    @include media("<=tablet") {
      width: 100%;
      order: 2;
      border-top: 1px solid rgb(49, 49, 49);
      padding: 5vh 0 0;
    }
    &--border {
      border-left: 1px solid rgb(49, 49, 49);
      padding-left: 3vw;
      padding-right: 0vh;
      @include media("<=tablet") {
        border: none;
        padding: 0 0 5vh;
        order: 1;
      }
    }
  }
  &__image-section {
    width: 100%;
    margin-bottom: 3vh;
    overflow: hidden;
  }
  &__image {
    transition: 0.5s;
    width: 100%;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
  }

  &__subtitle {
    font-size: 12px;
    text-transform: uppercase;
    margin: 0;
    letter-spacing: 1px;
    line-height: 3;
    font-weight: 500;

    &--big {
      letter-spacing: 0px;
      opacity: 1;
      font-weight: 500;
      font-size: 16px;
    }
  }
  &__text {
    font-size: 16px;
    margin-bottom: 0.2em;
    letter-spacing: 0.5px;
    font-weight: 200;
    line-height: 1.5;
    font-family: raleway;
    @include media("<=phone") {
      font-size: 14px;
    }
    &.bold {
      font-weight: 400;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    font-size: 14px;
    padding: 0;
    margin: 0;
    flex-wrap: wrap;

    &--pages {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 22px;
      @include media("<=tablet") {
        padding: 5vh 0;
      }
    }
    &-item {
      border: 1px solid var(--gray);
      color: var(--gray);
      padding: 5px 10px;
      margin: 5px 10px 5px 0;
      border-radius: 10px;
      transition: 0.4s ease-in-out;
      cursor: crosshair;
      &:hover {
        color: var(--black);
        background-color: #fff;
      }

      @include media("<=phone") {
        font-size: 10px;
      }

      &--more {
        margin: 0 20px 0 0;
        border: none;
        line-height: 100%;
        font-size: 24px;
        cursor: pointer;
        &:hover {
          color: var(--hover-color);
        }
      }
      @include media("<=phone") {
        font-size: 11px;
      }
    }
  }
  &__main-title {
    font-size: 24px;
    font-weight: 200;
    margin: 0.3em 0;
    @include media("<=phone") {
      font-size: 20px;
    }
  }

  &__technologies {
    justify-self: flex-end;
    margin: auto 0 2vh;
  }

  &__ulist {
    text-decoration: none;
    margin: 0 1em;
    padding: 0.4em 0.2em;

    &-item {
      font-family: Raleway;
      letter-spacing: 0.5px;
      font-weight: 200;
      line-height: 1.5;
      @include media("<=phone") {
        font-size: 14px;
      }
    }
  }
}

.github-logo {
  font-size: 42px;
}

.tab {
  font-family: Raleway;
  font-size: 22px;
  font-weight: 200;
  letter-spacing: 1px;
  opacity: 0.4;
  display: flex;
  color: var(--white);
  align-items: center;
  cursor: pointer;
  @include media("<=phone") {
    font-size: 18px;
  }
  @include media("<=tablet") {
    opacity: 1;
  }

  &-left {
    position: fixed;
    bottom: 5%;
    left: 3%;
    transform-origin: 50% 50%;
    &:hover {
      transform: translateX(-10px);
    }

    @include media("<=tablet") {
      position: static;
    }
  }
  &-right {
    position: fixed;
    bottom: 5%;
    right: 3%;
    transform-origin: 50% 50%;
    &:hover {
      transform: translateX(10px);
    }

    @include media("<=tablet") {
      position: static;
    }
  }
  &__icon {
    opacity: 1;
    color: var(--white);
  }
  span {
    display: inline;
    padding: 0 6px;
  }
  transition: 0.3s;
  &:hover {
    opacity: 1;
  }
}

.tabs {
  display: flex;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  top: 92%;
  z-index: 999;
  mix-blend-mode: difference;
}
