.contact-wrapper {
}

.contact {
  display: flex;
  padding: 5vh 0;
  position: relative;
  max-width: 1380px;
  margin: 0 auto;
  width: 100vw;
  overflow: hidden;

  @include media("<=tablet") {
    background-color: #131313;
  }

  &__title {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.9;
    position: relative;
    @include media("<=1380px") {
      font-size: 300px;
      padding: 0vh var(--padding-border);
    }
    @include media("<=600px") {
      display: none;
    }
  }

  &__absolute {
    font-size: 300px;
    opacity: 0.1;
    z-index: 300;
    mix-blend-mode: difference;
    user-select: none;
    margin-top: 25vh;

    @include media("<=1380px") {
      font-size: 250px;
    }
    @include media("<=desktop") {
      font-size: 180px;
      text-align: start;
    }
  }

  &__info {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(25%);

    @include media("<=1380px") {
      transform: translateY(-50%) translateX(0);
    }

    @include media("<=600px") {
      transform: translateY(0%) translateX(0);
      position: static;
    }
    // @include media("<=desktop") {
    //   font-size: 180px;
    // }
    // @include media("<=tablet") {
    //   font-size: 150px;
    // }
  }
  &__social {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 28px;
    cursor: pointer;
    &:hover {
      color: var(--hover-color);
    }

    @include media("<=tablet") {
      margin: 18px;
    }
  }

  &__icon {
    font-size: 42px;
    @include media("<=phone") {
      font-size: 28px;
    }
  }

  &__text {
    font-size: 24px;
    padding-left: 20px;
    opacity: 0.8;

    @include media("<=tablet") {
      font-size: 18px;
    }
    @include media("<=phone") {
      font-size: 14px;
    }
  }
}

