.about {
  max-width: 1380px;
  margin: 0 auto;
  padding: 10vh 0;
  display: flex;
  font-family: "Kamerik105";
  @include media("<=1380px") {
    padding: 8vh var(--padding-border);
  }
  @include media("<=desktop") {
    padding: 5vh var(--padding-border);
  }
  @include media("<=600px") {
    flex-direction: column;
    align-items: stretch;
  }
  &-wrapper {
    background-color: #191919;
  }

  &__tech {
    width: 40%;
    line-height: 1.8;

    @include media("<=600px") {
      order: 2;
      width: 100%;
      margin-bottom: 5vh;
    }
  }
  &__me {
    width: 60%;
    line-height: 2;
    letter-spacing: 0.2px;
    @include media("<=600px") {
      width: 100%;
      order: 1;
      margin-bottom: 5vh;
    }
    &-main {
      font-size: 22px;
      margin: 0.4em 0;
      @include media("<=tablet") {
        font-size: 18px;
      }
      @include media("<=phone") {
        font-size: 14px;
        font-weight: 300;
      }
    }
    &-secondary {
      font-size: 22px;
      color: var(--gray);
      padding-top: 0.5em;
      @include media("<=tablet") {
        font-size: 18px;
        font-weight: 200;
      }
      @include media("<=600px") {
        font-size: 16px;
        display: none;
      }
    }
  }

  &__title {
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 0.5px;
    @include media("<=phone") {
      font-size: 12px;
      font-family: montserrat;
    }
  }
}

.acordion {
  &-title {
    font-size: 42px;
    margin-top: 0.4em;
    cursor: pointer;
    display: flex;
    align-items: center;

    @include media("<=tablet") {
      font-size: 34px;
    }

    @include media("<=600px") {
      font-size: 24px;
    }

    &:hover .arrow-left {
      transform: rotate(35deg) scaleX(1.3);
    }
    &:hover .arrow-right {
      transform: rotate(-35deg) scaleX(1.3);
    }

    &.open2 {
      .arrow-left {
        transform: rotate(35deg) scaleX(1.3);
      }
    }
    &.open2 {
      .arrow-right {
        transform: rotate(-35deg) scaleX(1.3);
      }
    }
  }
  &-list {
    font-size: 20px;
    font-weight: 300;
    margin: 2px 0;
    letter-spacing: 1.5px;
    opacity: 0.8;
    width: 100%;
    padding-left: 56px;
    @include media("<=tablet") {
      font-size: 14px;
    }
    @include media("<=600px") {
      font-size: 14px;
      padding-left: 35px;
    }
  }
  &-menu {
    max-height: 0;
    overflow: hidden;
    transition: 1s ease-in-out;
    &.open {
      max-height: 1000px;
      transition-delay: 0.7s;
    }
  }
}

.arrow-left {
  width: 18px;
  height: 3px;
  background-color: var(--white);
  transition: 1s;

  @include media("<=600px") {
    width: 8px;
  }
}
.arrow-right {
  width: 18px;
  height: 3px;
  background-color: var(--white);
  margin-right: 20px;
  transition: 1s;

  @include media("<=600px") {
    width: 8px;
  }
}
