.header {
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  @include media("<=desktop") {
    padding-bottom: 0;
    overflow-x: hidden;
  }

  &__call {
    margin-top: 30vh;
    margin-bottom: 20vh;
    position: relative;
    mix-blend-mode: difference;
    z-index: 105;
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    font-weight: 300;
    cursor: default;
    user-select: none;
    transition: 0.5s transform;
    span {
      margin-right: 0.23em;
    }
    span:hover {
      color: var(--hover-color);
    }

    padding-left: 50px;
    border-left: 20px solid rgb(80, 80, 80);
    @include media("<=desktop") {
      margin: 15vh 0 10vh;
    }
    @include media("<=tablet") {
      padding-left: 15px;
      border-left: 10px solid rgb(80, 80, 80);
    }
    @include media("<=phone") {
      padding-left: 20px;
      border-left: 5px solid rgb(80, 80, 80);
    }
  }
  &__text {
    font-family: montserrat;
    font-size: 62px;
    font-weight: 300;
    letter-spacing: 0.2px;
    display: flex;
    position: relative;
    align-self: flex-start;
    z-index: 100;
    &-hi {
      position: relative;
      z-index: 999;
      overflow-y: hidden;
    }

    @include media("<=desktop") {
      font-size: 46px;
      flex-wrap: wrap;
    }
    @include media("<=tablet") {
      font-size: 38px;
      font-weight: 400;
    }
    @include media("<=600px") {
      font-size: 28px;
      line-height: 1.4;
    }
    @include media("<=phone") {
      font-size: 22px !important;
    }
    @include media("<=small") {
      font-size: 14px !important;
    }
  }

  &__image {
    height: 100%;
    width: 100%;
    position: absolute;
    @include media("<=desktop") {
      display: none;
    }
    img {
      position: absolute;
      mix-blend-mode: difference;
      z-index: 200;
      bottom: 10%;
      right: 4%;
      width: 500px;
      transition: translate 0.5s, opacity 0.4s, filter 0.4s;
      opacity: 0.1;
      transition: 0.4s;
      filter: grayscale(1);
      &:hover {
        opacity: 0.8;
        filter: grayscale(0);
      }
    }
  }

  &__button {
    text-decoration: none;
    color: var(--white);
    font-weight: 300;
    font-size: 24px;
    color: rgb(168, 168, 168);
    font-family: montserrat;
    &:hover {
      color: var(--hover-color);
      cursor: pointer;
    }

    @include media("<=desktop") {
      font-size: 22px;
      flex-wrap: wrap;
    }

    @include media("<=600px") {
      font-size: 16px;
    }
    @include media("<=small") {
      font-size: 12px;
    }
  }

  .callbacks {
    margin: 2vh 0 5vh;
    align-self: flex-start;

    @include media("<=phone") {
      margin: 2vh 0;
    }
  }

  .icons {
    display: flex;
    font-size: 40px !important;
    position: relative;
    z-index: 151;
    align-self: flex-start;

    @include media("<=600px") {
      font-size: 38px !important;
    }
    @include media("<=phone") {
      font-size: 26px !important;
    }
  }
  .icon {
    margin: 10px;
    color: var(--gray);
    &:hover {
      color: var(--hover-color);
      cursor: pointer;
    }
  }

  &__title {
    z-index: 700;
    position: absolute;
    bottom: 100px;
    left: 0;
    padding: 0;
    margin: 0;
    font-weight: 500;
    font-size: 148px;
    opacity: 0.03;
    color: var(--gray);
    font-family: "Anton", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    mix-blend-mode: difference;
    pointer-events: none;
    transform-origin: left bottom;

    transform: rotate(90deg);
    display: none;
  }
}
