@import "include-media/dist/_include-media.scss";
@import "./variables.scss";
@import "./Components/navigation.scss";
@import "./Components/header.scss";
@import "./Components/cursor.scss";
@import "./Components/about.scss";
@import "./Components/contact.scss";
@import "./Components/projects.scss";
@import "./Components/project-site.scss";

body {
  margin: 0;

  font-family: "PlusJakartaText", "montserrat", -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--black);
  color: var(--white);
  overscroll-behavior: none;
  -webkit-backface-visibility: hidden;
  // overflow-y: hidden;
  // visibility: hidden;
  // height: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "PlusJakartaDisplay";
  src: url("PlusJakartaDisplay-Light.ttf");
  font-weight: 100;
}
@font-face {
  font-family: "PlusJakartaText";
  src: url("PlusJakartaText-Light.ttf");
  font-weight: 100;
}

.wrapper {
  max-width: 1380px;
  margin: 0 auto;
  visibility: visible;
}
.header-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
#root {
  // visibility: inherit;
  // height:  inherit;
  // overflow-y:  inherit;
}

a {
  color: inherit;
  text-decoration: inherit;
}

.overlay {
  position: fixed;
  z-index: 800;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  user-select: none;
  pointer-events: none;
  &__section {
    height: 0;
    opacity: 1;
    position: absolute;
    width: 50%;
    top: 0;
    z-index: 5000;

    &.left {
      background-color: var(--overlay);
      left: 0;

      @include media("<=desktop") {
        width: 100%;
      }
    }
    &.right {
      right: 0;
      background-color: var(--overlay);
      @include media("<=desktop") {
        width: 100%;
      }
    }
    &.dark {
      background-color: rgb(20, 20, 20) !important;
    }
    &.light {
      background-color: var(--overlay2) !important;
      opacity: 0.2;
    }
  }
}

#scroller {
  height: 100vh;
}

.scrollUpContainer {
  position: fixed;
  right: -100px;
  bottom: 50%;
  transition: right 0.5s;
  cursor: pointer;
  font-size: 100px;
  padding: 10px;
  opacity: 0.4;
  cursor: pointer;
  transform: translateY(50%);
  mix-blend-mode: difference;
  @include media("<=desktop") {
    font-size: 60px;
    opacity: 1;
  }
}

.scrollUpTransition {
  right: 3%;
}

@font-face {
  font-family: "Kamerik105";
  src: url("https://uploads-ssl.webflow.com/5f4beb053a9c54d86a4f69b5/5f4cddbd9350b63684d6b8e3_Kamerik105-Book.woff")
    format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: auto;
}
@font-face {
