@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap");
.floating-left {
  position: absolute;
  // background-color: rgba(88, 88, 88, 0.438);
  width: 80%;
  height: 200px;
  left: -10px;
  bottom: -100px;
  z-index: -1;
}
.projects {
  margin: 0 auto;
  position: relative;

  &__title {
    position: relative;
    max-width: 1380px;
    margin: 8vh auto;
    &-text {
      padding: 2vh 0 2vh 50px;
      font-size: 102px;
      z-index: 2000;
      // opacity: 0.3;
      font-family: "Kamerik105";
      font-weight: 300;
      letter-spacing: 1.2px;
      pointer-events: none;
      user-select: none;
      color: var(--white);
      border-left: 20px solid rgba(255, 255, 255, 0.068);
      // border-bottom: 2px solid rgba(255, 255, 255, 0.068);
      @include media("<=desktop") {
        font-size: 72px;
      }
      @include media("<=tablet") {
        font-size: 52px;
        padding-left: 0.5em;
        border-bottom: none;
      }
      @include media("<=phone") {
        font-size: 38px;
      }
    }

    @include media("<=1380px") {
      margin: 5vh auto 2vh;
    }
  }
}

.project {
  max-width: 1380px;
  margin: 0vh auto;
  display: flex;
  align-items: stretch;
  position: relative;
  transition: 0.5s ease-in-out;
  padding: 15vh 0;
  border-bottom: 1px solid rgb(59, 59, 59);
  border-bottom: 1px solid rgb(44, 44, 44);

  @include media("<=1380px") {
    padding: 13vh var(--padding-border);
  }

  @include media("<=desktop") {
    flex-direction: column;
  }

  @include media("<=tablet") {
    padding: 10vh var(--padding-border);
  }

  &__info {
    width: 50%;
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 2vh 4vw 2vh 0vw;
    box-sizing: border-box;
    @include media("<=desktop") {
      width: 100%;
      padding: 0;
      margin-bottom: 5vh;
    }
  }
  &__image-section {
    width: 50%;
    display: flex;
    justify-content: center;
    align-self: center;
    // padding: 0 1%;
    box-sizing: border-box;
    @include media("<=desktop") {
      width: 100%;
    }
    position: relative;
    z-index: 2;
    // &::before {
    //   content: "";
    //   display: block;
    //   border-right: 1px solid #333;
    //   border-bottom: 1px solid #333;
    //   height: 90%;
    //   width: 100%;
    //   left: 10px;
    //   bottom: -10px;
    //   position: absolute;
    //   transition: all 0.3s ease-in;
    //   z-index: 1;
    //   user-select: none;
    //   pointer-events: none;
    // }
  }
  &__image {
    transition: 0.5s;
    width: 100%;
    cursor: pointer;
    opacity: 0.9;
  }

  &__date {
    font-size: 12px;
    color: var(--gray);
    font-weight: 400;
    font-family: montserrat;
    @include media("<=phone") {
      font-size: 10px;
      text-align: left;
    }
  }

  &__title {
    font-size: 72px;
    font-weight: 300;
    line-height: 2;
    font-family: raleway;
    margin: 0;
    position: relative;
    span {
      position: relative;
      font-weight: 100;
    }

    @include media("<=desktop") {
      font-size: 54px;
    }
    @include media("<=phone") {
      font-size: 36px;
      line-height: 2.4em;
    }
  }

  &__subtitle {
    font-size: 12px;
    text-transform: uppercase;
    margin: 0;
    opacity: 0.9;
    line-height: 3.4;
    font-weight: 500;
    opacity: 0.4;

    @include media("<=desktop") {
      font-size: 10px;
    }
  }
  &__text {
    font-size: 17px;
    margin: 0;
    letter-spacing: 0.4px;
    line-height: 1.6;
    font-weight: 300;
    font-family: montserrat;
    @include media("<=tablet") {
      font-size: 14px;
      padding: 5px 0;
      line-height: 1.4;
      letter-spacing: 1.4;
    }
    @include media("<=phone") {
      font-size: 14px;
    }
    @include media("<=small") {
      font-size: 10px;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    font-size: 14px;
    padding: 0;
    margin: 0;
    flex-wrap: wrap;

    &--pages {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 22px;
      font-weight: 300;
      letter-spacing: 1px;
    }
    &-item {
      border: 1px solid #777;
      color: var(--white);
      padding: 5px 10px;
      margin: 5px 10px 5px 0;
      border-radius: 10px;
      opacity: 0.4;
      user-select: none;
      transition: 0.4s ease-in-out;
      &:hover {
        opacity: 0.9;
      }
      @include media("<=desktop") {
        font-size: 12px;
      }
      @include media("<=phone") {
        font-size: 9px;
      }
      &--more {
        font-weight: 300;
        letter-spacing: 0.2px;
        margin: 0 20px;
        display: inline;
        cursor: pointer;
        font-family: "Kamerik105";
        padding: 0.5em 0.8em;
        font-size: 22px;
        border-radius: 5px;
        transition: cubic-bezier(0.075, 0.82, 0.165, 1);
        &:hover {
          color: var(--hover-color);
        }

        @include media("<=phone") {
          font-size: 14px;
          margin: 0;
          margin-right: 5vw;
        }
        &.pl {
          display: inline;
          @include media("<=phone") {
            margin: 1.5vh 20px;
            font-size: 15px;
            font-weight: 200;
          }
        }
      }
    }
  }

  &__technologies {
    justify-self: flex-end;
    margin: 3vh 0 0;
    @include media("<=1380px") {
      margin: 3vh 0 0;
    }
    @include media("<=phone", ">=600px") {
      margin: 15vh 0 0;
    }
  }
}

.github-logo {
  font-size: 42px;
}

#project5 {
  border-bottom: none;
  @include media(">=tablet") {
    padding-bottom: 0;
  }
}
