.navigation {
  &-wrapper {
    background-color: var(--black);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 160;
    transition: 1s;
    width: 100vw;
  }
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
  margin: auto;
  max-width: 1380px;
  padding: 2vh 0;
  @include media("<=1380px") {
    padding: 2vh var(--padding-border);
  }
  &__logo {
    display: flex;
    align-items: center;
    &-text {
      color: var(--white);
      font-weight: 100;
      padding: 0;
      margin: 0;
      cursor: pointer;
      font-size: 20px;
      font-family: "Kamerik105";
      text-decoration: none;
      @include media("<=phone") {
        font-size: 18px;
      }
    }
  }
  &__menu {
    display: flex;
    align-items: stretch;
    font-family: "Kamerik105";

    @include media("<=tablet") {
      background-color: #181818;
    }
    &-item {
      text-align: center;
      font-size: 12px;
      color: var(--white);
      font-weight: 100;
      text-decoration: none;
      letter-spacing: 2px;
      display: block;
      margin-right: 2em;
      transition: 0.2s;
      cursor: pointer;
      &:hover {
        color: var(--hover-color);
      }
      @include media("<=tablet") {
        margin-right: 0;
        padding: 3vh 0;
      }
    }

    @include media("<=tablet") {
      flex-direction: column;
    }

    &--desktop {
      @include media("<=tablet") {
        display: none;
      }
    }
    &--mobile {
      display: none;
      @include media("<=tablet") {
        display: flex;
      }
    }
  }

  &__hamburger {
    display: none;
    @include media("<=tablet") {
      display: block;
    }
    align-self: center;
    margin-left: auto;
    overflow: hidden;

    &-icon {
      @include media("<=tablet") {
        font-size: 6px !important;
        opacity: 0.7;
      }
    }
  }
}

.language {
  text-align: center;
  font-size: 14px;
  color: var(--white);
  font-weight: 100;
  text-decoration: none;
  transition: 0.2s;
  letter-spacing: 0.5px;
  cursor: pointer;
  transform: scale(0.8);
  &__item {
    display: inline;
    margin: 0 5px;
    padding-bottom: 5px;
    &.active {
      font-weight: 400;
      color: var(--white);
      border-bottom: 2px solid var(--hover-color);
    }
    transition: 0.3s;
    &:hover {
      font-weight: 500;
    }
  }
  @include media("<=tablet") {
    font-size: 14px;
    margin-right: 0;
    padding: 2vh 0;
  }
}
